console.log(mobileCheck());
const options = { disableHysteresis: true, threshold: 0 };
function HideOnScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger({ threshold: 0 });
  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}
const Cappbar = (props) => {
  const classes = headStyle();
  const res = useScrollTrigger(options);
  let location = useLocation();

  const { children } = props;

  return res !== true &&
    (location.pathname === "/" || location.pathname === "/signin") ? (
    <AppBar {...props} color="transparent" elevation={0}>
      {children}
    </AppBar>
  ) : props.token ? (
    <AppBar {...props} color="transparent" className={classes.dashheader}>
      {children}
    </AppBar>
  ) : mobileCheck() ? (
    <HideOnScroll {...props}>
      <AppBar
        {...props}
        color="default"
        style={{
          borderRadius: "0 0 1rem 1rem",
          boxShadow:
            "0px 3px 3px -2px rgb(0 0 0 / 20%), 0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%) !important",
          backdropFilter: "blur(6px)",
          backgroundColor: "rgba(255, 255, 255, 0.72)",
        }}
      >
        {children}
      </AppBar>
    </HideOnScroll>
  ) : (
    <AppBar
      {...props}
      color="default"
      style={{
        borderRadius: "0 0 1rem 1rem",
        boxShadow:
          "0px 3px 3px -2px rgb(0 0 0 / 20%), 0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%) !important",
        backdropFilter: "blur(6px)",
        backgroundColor: "rgba(255, 255, 255, 0.72)",
      }}
    >
      {children}
    </AppBar>
  );
};

function Header(props) {
  const history = useHistory();
  let location = useLocation();
  // const theme = useTheme();
  const [open, setOpen] = useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };
  const theme = createTheme({});
  const classes = headStyle();

  return (
    <div className={classes.grow}>
      <Cappbar position="fixed" {...props} open={props.open}>
        {props.token ? (
          <MuiThemeProvider theme={theme}>
            <HeaderLogged
              handleDrawerOpen={handleDrawerOpen}
              open={props.open}
              {...props}
            />
          </MuiThemeProvider>
        ) : (
          <HeaderHome handleDrawerOpen={handleDrawerOpen} open={props.open} />
        )}
      </Cappbar>
      <Drawe
        history={history}
        classes={classes}
        location={location}
        handleDrawerClose={handleDrawerClose}
        theme={theme}
        open={open}
      />
    </div>
  );
}

export default Header;
